<template>
    <div class="pageComponent">
        <h1>{{getTitle}}</h1>
        <h1>{{getMemo}}</h1>
    </div>
</template>

<script>


export default {
    name : 'PageComponent', 
    computed : {
        getTitle() {
            return this.$store.state.list[this.$route.params.id-1].title
        },
        getMemo() {
            return this.$store.state.list[this.$route.params.id-1].memo
        }
    }
}
</script>

